.navbar-link{
    padding: 0.5em;
    color: white;
}

.main-container{
    padding-top: 3em;
}

.main-container .row+.row{
    margin-top: 9em;
}

#experience-section .timeline {
    list-style-type: none;
    position: relative;
}
#experience-section .timeline:before {
    content: ' ';
    background: #d4d9df;
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    height: 100%;
    z-index: 400;
}
#experience-section ul.timeline > li {
    margin: 20px 0;
    padding-left: 20px;
}
#experience-section ul.timeline > li:before {
    content: ' ';
    background: white;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 3px solid #6c757d;
    left: 20px;
    width: 20px;
    height: 20px;
    z-index: 400;
}